@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@500&family=Quicksand:wght@400;500&family=Roboto:ital,wght@0,400;0,500;1,100&display=swap');

html {
  scroll-behavior: smooth;
}

.App {
  height: 100%;
  padding: 0;
  margin: 0;
  background-position: cover;
  background-size: center;
  background-repeat: repeat;
  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("https://wallpaperaccess.com/full/439760.jpg");
}